import React, { useRef } from 'react'

export const Pads = ({ pad }) => {

    const btnRef = useRef()

    const padClick = async () => {
        await pad

        const initColor = '#3e486c'
        const playColor = '#0026e2cc'

        let color = ''
        if (pad.isPlay) color = playColor
        else color = initColor
        btnRef.current.style.backgroundColor = color
    }

    return (
        <div>
            <button onClick={padClick} >
                <span>{pad.name.toUpperCase()}</span>
                <div ref={btnRef} className="wave"></div>
            </button>
        </div>
    )
}
