
export const looperService = {
    stopLoop,
    startLoop,
    updateLoop,
    saveRecording,
    getRecording
}

let audioArr = []

function stopLoop(pads) {
    stopAudio()

    pads.map(pad => {
        if (pad.isPlay) pad.isPlay = false
    })
    return pads
}

function startLoop(pads) {
    pads.forEach(pad => {
        const { url } = pad
        const path = require(`../assets/audio/${url}.mp3`).default
        const audio = new Audio(path)
        audio.play()

        audioArr.push(audio)
    })
}

function updateLoop(loop, pad) {
    if (pad.isPlay) loop.push(pad)
    else {
        const idx = loop.findIndex(item => item.name === pad.name)
        loop.splice(idx, 1)
    }
    return loop
}

function stopAudio() {
    audioArr.forEach(audio => {
        audio.pause()
    })
}

function saveRecording(loop) {
    const KEY = 'recording'
    const currLoops = _loadFromStorage(KEY)

    if (!currLoops) {
        _saveToStorage(KEY, [loop])
        return
    } else {
        currLoops.push(loop)
        _saveToStorage(KEY, currLoops)
    }
}

function getRecording() {
    const KEY = 'recording'
    return _loadFromStorage(KEY)
}

function _saveToStorage(key, data) {
    var json = JSON.stringify(data);
    localStorage.setItem(key, json)
}

function _loadFromStorage(key) {
    var json = localStorage.getItem(key)
    var data = JSON.parse(json)
    return data;
}


