import { useEffect, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Pads } from '../cmps/Pads'
import { saveLoopChanges, savePadsChanges } from '../store/actions/looperActions'

export const LooperApp = () => {

  const { pads } = useSelector(state => state.looperModule)
  const { loopPlaying } = useSelector(state => state.looperModule)
  const { isStop } = useSelector(state => state.looperModule)

  const dispatch = useDispatch()
  const refUl = useRef()


  useEffect(() => {
    if (isStop) clearBgc()
  }, [isStop])

  const clickPad = (pad) => {
    pad.isPlay = !pad.isPlay

    dispatch(saveLoopChanges(loopPlaying, pad))
    dispatch(savePadsChanges(pads))
  }

  const clearBgc = () => {
    let initColor = '#3e486c'
    let pads = refUl.current.children

    for (var i = 0; i < pads.length; i++) {
      pads[i].children[0].children[0].lastChild.style.backgroundColor = initColor
    }
  }

  
  return (
    <div className="box">
      <div className="container">
        {
          pads &&
          <ul ref={refUl} >
            {
              pads.map((pad, idx) => (
                <li onClick={() => clickPad(pad)} key={idx}>
                  <Pads pad={pad}></Pads>
                </li>
              ))
            }
          </ul>
        }
      </div>
    </div>
  )
}
