import { looperService } from '../../services/looperService'


export function savePadsChanges(pads) {
  return async dispatch => {
    dispatch({ type: 'SET_SAVE_CHANGES', pads })
  }
}

export function stopLoop(padsToStop) {
  return async dispatch => {
    const pads = looperService.stopLoop(padsToStop)
    const emptyArr = []
    const isStop = true
    const isRecording = false

    dispatch({ type: 'SET_SAVE_CHANGES', pads })
    dispatch({ type: 'SET_CLEAR_LOOP', emptyArr })
    dispatch({ type: 'SET_IS_STOP', isStop })
    dispatch({ type: 'SET_IS_RECORDING', isRecording })
  }
}

export function startLoop(pads) {
  return async dispatch => {
    looperService.startLoop(pads)

    const isStop = false
    dispatch({ type: 'SET_IS_STOP', isStop })
  }
}

export function saveLoopChanges(loop, pad) {
  return async dispatch => {
    const updateLoop = looperService.updateLoop(loop, pad)
    dispatch({ type: 'SET_SAVE_LOOP', updateLoop })
  }
}

// --------------------------------------------------Recording:

export function toggleRecord(isRecordingLoop) {
  return async dispatch => {
    const isRecording = !isRecordingLoop
    if (isRecording) localStorage.clear()
    dispatch({ type: 'SET_IS_RECORDING', isRecording })
  }
}

export function saveLoopInRecordArr(loop) {
  return async (dispatch, getState) => {

    const { isRecording } = getState().looperModule
    if (isRecording) looperService.saveRecording(loop)
  }
}

export function geyLoopInRecordArr() {
  return async dispatch => {
    return looperService.getRecording()
  }
}
