export function looperReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case 'SET_SAVE_CHANGES':
      return {
        ...state,
        pads: action.pads
      }
    case 'SET_SAVE_LOOP':
      return {
        ...state,
        pads: action.updateLoop
      }
    case 'SET_CLEAR_LOOP':
      return {
        ...state,
        loopPlaying: action.emptyArr
      }
    case 'SET_IS_STOP':
      return {
        ...state,
        isStop: action.isStop
      }
    case 'SET_IS_RECORDING':
      return {
        ...state,
        isRecording: action.isRecording
      }
    default:
      return state
  }
}