import { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { geyLoopInRecordArr, saveLoopInRecordArr, startLoop, stopLoop, toggleRecord } from '../store/actions/looperActions';

import IconPlay from '@material-ui/icons/PlayCircleOutline';
import IconPause from '@material-ui/icons/PauseCircleOutline';
import Button from '@material-ui/core/Button';


export const AppHeader = () => {

  const dispatch = useDispatch()

  const [saveInterval, setSaveInterval] = useState(null)
  const [recordLoop, setRecordLoop] = useState([])
  const [errMsg, setErrMsg] = useState(null)

  const { pads } = useSelector(state => state.looperModule)
  const { loopPlaying } = useSelector(state => state.looperModule)
  const { isStop } = useSelector(state => state.looperModule)
  const { isRecording } = useSelector(state => state.looperModule)

  const refStartLoop = useRef()
  const refStopLoop = useRef()


  const stopAudio = () => {
    if (isStop) return console.log('msg');

    refStartLoop.current.style.color = 'white'

    clearInterval(saveInterval)
    dispatch(stopLoop(pads))
  }

  const startAudio = () => {
    if (!loopPlaying.length || !isStop) return showErrMsg('There is a problem with the audio at the moment')

    refStartLoop.current.style.color = '#d400d4'

    const interval = setInterval(function firstTime() {
      dispatch(startLoop(loopPlaying))

      saveToRecordArr()
      if (!recordLoop.length) setRecordLoop([loopPlaying])
      else {
        setRecordLoop(prev => [...prev, loopPlaying])
      }

      return firstTime;
    }(), 8000);

    setSaveInterval(interval)
  }

  const saveToRecordArr = () => {
    dispatch(saveLoopInRecordArr(loopPlaying, isRecording))
  }

  const toggleRecording = () => {
    dispatch(toggleRecord(isRecording))
  }

  const playRecord = async () => {
    const recording = await dispatch(geyLoopInRecordArr())

    if (!isStop || !recording) return showErrMsg('There is a problem with the recording at the moment')

    let count = 0

    const interval = setInterval(function firstTime() {
      if (count === recording.length) {
        clearInterval(interval)
        return
      }

      dispatch(startLoop(recording[count]))
      count++

      return firstTime;
    }(), 8000);
  }

  const showErrMsg = (txt) => {
    setErrMsg(txt)
    setTimeout(() => {
      setErrMsg(null)
    }, 3000);
  }


  return (
    <div className="app-header">
      <section className="icons">
        <IconPlay onClick={startAudio} ref={refStartLoop} />
        <IconPause onClick={stopAudio} ref={refStopLoop} />
      </section>

      <section>
        <Button onClick={toggleRecording} variant="outlined" color={isRecording ? "secondary" : "primary"}>{isRecording ? 'Stop' : 'Start'} recording</Button>
        <Button onClick={playRecord} variant="outlined" color="primary">Play Record</Button>
      </section>

      {errMsg && <article className="err-msg">{errMsg}</article>}
    </div>
  )
}

